import uniLoadMore from '../../components/uni-load-more.vue'
export default {
	data() {
		return {
			title: 'uni-load-more',
			list: [],
			loadingType: 0,
			contentText: {
				contentdown: "上拉显示更多",
				contentrefresh: "正在加载...",
				contentnomore: "没有更多数据了"
			}
		}
	},
	onLoad() {
		//let list = [];
		uni.request({
			url: 'https://unidemo.dcloud.net.cn/api/news',
			method: 'GET',
			data: {},
			success: res => {
				console.log("新闻列表:"+JSON.stringify(res.data));
				let list = res.data;
				this.list = list;
			},
			fail: () => {},
			complete: () => {}
		});
	},
	
	onReachBottom() {
		if (this.loadingType !== 0) {
			return;
		}
		this.loadingType = 1;
		let list = [],
		//let list = res.data;
			maxItem = this.list[this.list.length - 1],
			length = maxItem + 6;
		for (let i = maxItem + 1; i < length; i++) {
			list.push(i);
		}
		setTimeout(() => {
			if (length > 26) {
				this.loadingType = 2;
				return;
			}
			this.list = this.list.concat(list);
			this.loadingType = 0;
		}, 800);
	},
	components: {
		uniLoadMore
	},
	methods:{
		
	}
}
